import Blogsdetails from "@/Components/Blogs/Blogdetails";
import { GetAddonsList } from "@/utilities/cookies";
import { notFound } from "next/navigation";
import { blogsseo } from "@/api/serversideaction/category";
import { GetToken } from "@/utilities/cookies";
import createMetadata from "@/Components/Seo/createMetadata";
import { headers } from "next/headers";
import { cleanText } from "@/utilities/globalFunctions";
import StructuredData from "@/Components/Seo/StructuredData";
import createStructuredData from "@/Components/Seo/createStructuredData";

export const dynamic = "force-dynamic";

const getBlogDetailsFn = async () => {
  try {
    return (await import("../../../../addOns/Blogs/blogs")).blogsdetails;
  } catch {
    return null;
  }
};

export async function generateMetadata(props) {
  const params = await props.params;
  const h = await headers();
  const host = h.get("host");
  const proto = h.get("x-forwarded-proto") || "https";
  const base = `${proto}://${host}`;

  try {
    const addons = await GetAddonsList().catch(() => null);

    // If blog addon is explicitly disabled → return empty metadata
    if (addons?.blog && addons.blog.status === 0) {
      return createMetadata({});
    }

    const blogsdetails = await getBlogDetailsFn();
    const seoEnabled = addons?.seo?.status === 1;

    // Wrap API calls in try-catch to prevent errors from causing 404s
    const [seoRes, blogRes] = await Promise.all([
      seoEnabled
        ? blogsseo(params.slug).catch((err) => {
            console.error("Blog SEO API error:", err);
            return { data: {} };
          })
        : Promise.resolve({ data: {} }),
      blogsdetails
        ? blogsdetails(params.slug).catch((err) => {
            console.error("Blog details API error:", err);
            return { data: [] };
          })
        : Promise.resolve({ data: [] }),
    ]);

    const seo = seoRes?.data || {};
    const blog = blogRes?.data?.[0] || {};

    // PRIORITY: SEO → Blog → Slug
    const title =
      seo.metaTagTitle?.slice(0, 60) || blog.title?.slice(0, 60) || params.slug;
    const description = cleanText(
      seo.metaTagDescription ||
        blog.description ||
        `Explore ${title}`,
    );

    const keywords = seo.metaTagKeyword || `${params.slug}, blog, article`;

    // IMAGE PRIORITY: SEO → BLOG → FALLBACK LOGO
    const image = seo.ogImage
      ? `${process.env.NEXT_PUBLIC_IMAGE_URL}?path=${seo.ogImagePath}&name=${seo.ogImage}&width=1200&height=630`
      : blog.image
      ? `${process.env.NEXT_PUBLIC_IMAGE_URL}?path=${blog.imagePath}&name=${blog.image}&width=1200&height=630`
      : `${base}/img/logo.svg`;

    return createMetadata({
      title,
      description,
      keywords,
      imageUrl: image,
      url: `${base}/blog/${params.slug}`,
      baseUrl: base,
      showRobots: true,
    });
  } catch (error) {
    console.error("Blog metadata error:", error);

    return createMetadata({
      title: params.slug,
      description: `Explore ${params.slug}`,
      keywords: params.slug,
      imageUrl: `${base}/img/logo.svg`,
      url: `${base}/blog/${params.slug}`,
      baseUrl: base,
      showRobots: true,
    });
  }
}

export default async function page(props) {
  const params = await props.params;
  const [token, addonList] = await Promise.all([GetToken(), GetAddonsList()]);

  // Only 404 if blog addon is explicitly disabled
  if (addonList && addonList?.["blog"]?.status === 0) {
    notFound();
  }
const headersList = await headers();
  const host = headersList.get("host");
  const protocol = headersList.get("x-forwarded-proto") || "https";
  const baseUrl = `${protocol}://${host}`;

  // Fetch Blog Details
  const blogsdetails = await getBlogDetailsFn();
  const blogRes = blogsdetails ? await blogsdetails(params.slug) : {};
  const blog = blogRes?.data?.[0] || {};
  const title = blog.title?.slice(0, 60) || params.slug;
  const description = cleanText( blog.description || `Explore ${title}`);

  // Build image URL
  const imageUrl = blog?.image
    ? `${process.env.NEXT_PUBLIC_IMAGE_URL}?path=${blog?.imagePath}&name=${blog?.image}&width=1200&height=630`
    : `${baseUrl}/img/logo.svg`;

  //  Generate BlogPosting Schema using your function
  const schema = createStructuredData({
    type: "BlogPosting",

    // Basic meta
    title: title,
    description: description,
    url: `${baseUrl}/blog/${params.slug}`,
    image: imageUrl,

    // Dates
    datePublished: blog?.created_at,
    dateModified: blog?.updated_at || blog?.created_at,

    // Author (fallback = organization)
    author: {
      "@type": "Organization",
      name: process.env.NEXT_PUBLIC_SEO_TITLE || "Website",
    },

    // Publisher
    publisher: {
      "@type": "Organization",
      name: process.env.NEXT_PUBLIC_SEO_TITLE || "Website",
      logo: {
        "@type": "ImageObject",
        url: `${baseUrl}/img/logo.svg`,
      },
    },

    // Optional keywords
    keywords: blog?.keywords ? blog.keywords.split(",") : [],

    // Breadcrumbs
    breadcrumbs: [
      { name: "Home", url: baseUrl },
      { name: "Blog", url: `${baseUrl}/blog` },
      { name: blog?.title || params.slug, url: `${baseUrl}/blog/${params.slug}` }
    ],

    // Custom BlogPosting fields
    customData: {
      headline: blog?.title,
      mainEntityOfPage: {
        "@type": "WebPage",
        "@id": `${baseUrl}/blog/${params.slug}`,
      },
    },
  });
  // If addonList/blog key is missing (e.g., no cookies on curl -I),
  // assume blogs are available and still render the page.
  return (
    <>
      <StructuredData data={schema} />

       <Blogsdetails token={token} params={params.slug} />
    </>
  )
 ;
}
