// Next
import { notFound } from "next/navigation";

// Components
import ConnectPlugin from "@/Components/connectPlugins";
import { blogsdetails } from "../../../../addOns/Blogs/blogs";

export default async function Blogsdetails({ token, params }) {
  const blogDetail = await blogsdetails(params);

  if (blogDetail?.data?.length == 0) {
    notFound();
  }

  return (
    ConnectPlugin.Blogsdetails && (
      <ConnectPlugin.Blogsdetails token={token} params={params} />
    )
  );
}
