
import axiosInstance from "@/api/interceptor";

//blog-list
export const blogslist = async (limit = 9, offset = 0) => {
  const data = await axiosInstance.get(`list/blog/blog-list?limit=${limit}&offset=${offset}`);
  return data?.data;
};
//blog-count
export const getBlogCount = async () => {
  const data = await axiosInstance.get(`list/blog/blog-list?count=1`);
  return data?.data;
};


//blog-details
export const blogsdetails = async (slug) => {
  const data = await axiosInstance.get(`list/blog/blog-detail/${slug}`);
  return data?.data;
};

// Blog-SEO
export const blogsseo = async (slug) => {
  const data = await axiosInstance.get(`/seo/blog/${slug}`);
  return data?.data;
};

// related Blogs
export const RelatedBlogsApi = async (slug) => {
  const data = await axiosInstance.get(
    `list/related-blog-list?blogSlug=${slug}`
  );
  return data?.data;
};
