export function getReplacedDescriptionValue(description) {
  if (typeof window !== "undefined") {
    const decodeHtmlEntities = (str) => {
      const txt = document.createElement("textarea");
      txt.innerHTML = str;
      return txt.value;
    };

    const sanitizedDescription = decodeHtmlEntities(description)
      ?.replaceAll("&amp;", "&")
      .replaceAll("&lt;", "<")
      .replaceAll("&gt;", ">")
      .replaceAll("&quot;", '"')
      .replaceAll("&#39;", "'")
      .replaceAll("&sbquo;", "‚")
      .replaceAll("&#61;", "=")
      .replaceAll("&#45;", "-")
      .replaceAll("&hellip;", "…")
      .replaceAll("&commat;", "@")
      .replaceAll("&copy;", "©")
      .replaceAll("&#35;", "#")
      .replaceAll("&ldquo;", "“")
      .replaceAll("&rsquo;", "’")
      .replaceAll("&lsquo;", "‘")
      .replaceAll("&trade;", "™")
      .replaceAll("&reg;", "®")
      .replaceAll("&ndash;", "–")
      .replaceAll("&eacute;", "é")
      .replaceAll("&euro;", "€")
      .replaceAll("&pound;", "£")
      .replace("&lt;/p&gt;&amp;&#35;10;&amp;&#35;10;&lt;p&gt;", "");

    return sanitizedDescription;
  }
}
export const cleanText = (txt = "") => {
  try {
    if (typeof txt !== "string") txt = String(txt || "");

    // Decode basic HTML entities
    const decoded = txt
      .replace(/&lt;?/g, "<")
      .replace(/&gt;?/g, ">")
      .replace(/&amp;?/g, "&");

    const cleaned = decoded
      .replace(/<[^>]*>/g, "") // remove HTML tags
      .replace(/[^\w\s.,-]/g, "") // remove special characters
      .replace(/\s+/g, " ") // collapse spaces
      .trim();

    return cleaned.slice(0, 160);
  } catch (error) {
    console.error("cleanText error:", error);
    return "";
  }
};

// ✅ Sort function for topcategories and all children
export function sortCategories(categories) {
  return categories
    ?.slice()
    ?.sort((a, b) => {
      if (a?.sortOrder !== b?.sortOrder) {
        return a?.sortOrder - b?.sortOrder;
      }
      const nameA = a?.name?.toLowerCase() || "";
      const nameB = b?.name?.toLowerCase() || "";
      return nameA?.localeCompare(nameB);
    })
    ?.map((category) => ({
      ...category,
      children: category?.children ? sortCategories(category?.children) : [],
    }));
}
export const debounce = (func, delay = 500) => {
  let timeout;
  return (...args) => {
    clearTimeout(timeout);
    timeout = setTimeout(() => func(...args), delay);
  };
};
