// Create comprehensive structured data for SEO with support for multiple schema types

export default function createStructuredData({
  title,
  description,
  url,
  image,
  keywords = [],
  datePublished,
  dateModified,
  dateCreated,
  author,
  publisher,
  type = "WebPage",
  customData = {},

  // Product fields
  price,
  currency = "USD",
  availability = "https://schema.org/InStock",
  brand,
  sku,
  mpn,
  category,
  aggregateRating,
  review,
  offers,

  // Organization fields
  organizationName,
  organizationLogo,
  organizationUrl,

  // Breadcrumb fields
  breadcrumbs,
}) {
  const nowISO = new Date().toISOString();

  // Format date safely
  const formatDate = (value) => {
    if (!value) return nowISO;
    try {
      return new Date(value).toISOString();
    } catch {
      return value;
    }
  };

  const isoPublished = formatDate(datePublished || dateCreated);
  const isoModified = formatDate(dateModified || nowISO);

  // Base schema
  let schema = {
    "@context": "https://schema.org",
    "@type": type,
    name: title,
    description: description,
    url: url,
    image: image,
  };

  // Keywords support
  if (keywords?.length > 0) {
    schema.keywords = keywords.join(", ");
  }

  // Dates for articles/pages
  if (["Article", "BlogPosting", "WebPage"].includes(type)) {
    schema.datePublished = isoPublished;
    schema.dateModified = isoModified;
  }

  // Author
  if (author) {
    schema.author =
      typeof author === "string"
        ? { "@type": "Person", name: author }
        : author;
  }

  // Publisher / Organization
  if (publisher) {
    schema.publisher = publisher;
  } else if (organizationName) {
    schema.publisher = {
      "@type": "Organization",
      name: organizationName,
      ...(organizationLogo && {
        logo: { "@type": "ImageObject", url: organizationLogo },
      }),
      ...(organizationUrl && { url: organizationUrl }),
    };
  }

  // Product Schema
  if (type === "Product") {
    schema = {
      ...schema,
      "@type": "Product",
      ...(brand && {
        brand: {
          "@type": "Brand",
          name: typeof brand === "string" ? brand : brand?.name,
        },
      }),
      ...(category && { category }),
      ...(sku && { sku }),
      ...(mpn && { mpn }),

      ...(aggregateRating && {
        aggregateRating: {
          "@type": "AggregateRating",
          ratingValue: aggregateRating.ratingValue,
          reviewCount: aggregateRating.reviewCount,
          bestRating: aggregateRating.bestRating || 5,
          worstRating: aggregateRating.worstRating || 1,
        },
      }),

      ...(review?.length > 0 && {
        review: review.map((r) => ({
          "@type": "Review",
          author: { "@type": "Person", name: r.author || "Customer" },
          datePublished: formatDate(r.datePublished),
          reviewBody: r.reviewBody,
          reviewRating: {
            "@type": "Rating",
            ratingValue: r.ratingValue,
            bestRating: r.bestRating || 5,
            worstRating: r.worstRating || 1,
          },
        })),
      }),

      offers: offers || {
        "@type": "Offer",
        url: url,
        priceCurrency: currency,
        ...(price && { price: price.toString() }),
        availability: availability,
        itemCondition: "https://schema.org/NewCondition",
      },
    };
  }

  // Organization Schema
  if (type === "Organization") {
    schema = {
      ...schema,
      "@type": "Organization",
      ...(organizationLogo && {
        logo: { "@type": "ImageObject", url: organizationLogo },
      }),
    };
  }

  // BreadcrumbList
  if (breadcrumbs?.length > 0) {
    schema.breadcrumb = {
      "@type": "BreadcrumbList",
      itemListElement: breadcrumbs.map((crumb, i) => ({
        "@type": "ListItem",
        position: i + 1,
        name: crumb.name,
        item: crumb.url,
      })),
    };
  }

  // Merge Custom Data
  return {
    ...schema,
    ...customData,
  };
}

